.cat-list {
	list-style-type: none;
	padding: 0;
	margin: 0;

	font-size: 16px;
	font-weight: 300;

	display: grid;
	gap: 20px;
}

.cat-list li.active {
	text-decoration: underline;
}

.cat-list a {
	color: #000;
	text-decoration: none;
}

.cat-list a:hover {
	text-decoration: underline;
}